import searchBox from '@/components/searchTopBox';
export default {
	components: {
		searchBox,
	},
	data() {
		return {
			inpVal:"",
			historyData:[
				{"id":1,"name":"商品"},
				{"id":2,"name":"水果拼盘"},
				{"id":3,"name":"小清新的衣服"},
			]
		};
	},
	props:{
		
	},
	created() {
		
	},
	methods:{
		searchFn(){
			let val = this.$children[0].iptval;
			let addData =[];
			if(val !=""){
				let list = this.historyData;
				let isExist = false;
				for(let i in list){
					if(list[i].name == val){
						isExist = true;
						break;
					}
				}
				if(!isExist){
					let len = this.historyData.length;
					addData.push({"id":len,"name":val});
				}
				this.historyData = this.historyData.concat(addData);
				uni.navigateTo({
					url: '/pages/product/list'
				})
			}
		},
		delHistory(){
			this.historyData = [];
		},
	}
}
